* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--bodyFont);
  background-color: var(--bodyBg);
  /* background-color: #33bfff !important; */
  font-size: 16px;
  font-weight: 400;
  line-height: 1.8;
  position: relative;
}

p {
  font-size: 16px;
  margin-bottom: 15px;
  color: var(--contentColor);
}

a {
  text-decoration: none;
  transition: var(--transition);
}

p,
a,
.btn,
button,
p,
input,
select,
textarea,
li,
img {
  -webkit-transition: all var(--transition) ease-out 0s;
  -moz-transition: all var(--transition) ease-out 0s;
  -ms-transition: all var(--transition) ease-out 0s;
  -o-transition: all var(--transition) ease-out 0s;
